.card{
    &__container {
        height: 374px;
        width: 350px;
        background-color: white;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
    }

    &__header {
        height: 33%;
        background-image: url('../images/bg-pattern-card.svg');
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
    }

    &__info {
        height: 33%;

        &--container {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            margin-top: -14%;

            & > img {
                border-radius: 100%;
                border: 5px solid white;
                background-color: white;
                width: 96px;
                height: auto;
            }
        }
    }

    &__stats {
        height: 33%;
        display: flex;
        justify-content: space-evenly;
        align-items: center;

        &--box{
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }

}