@use './card.scss';

*{
    margin: 0;
    padding: 0;
}

html {
    box-sizing: border-box;
}


body {
    background-color: #19a1ae;
    font-family: 'Kumbh Sans', sans-serif;
    font-weight: 400;
    min-height: 350px;
}
.circlegrid {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: 0;

    &__top{
        position: absolute;
        transform: translate(-25%, -25%);
    }

    &__bottom {
        position: relative;
        transform: translate(50%, 50%);
    }
}
.container {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    
    & > * {
        z-index: 1;
    }
}

.block {
    display: block;
}

.bold {
    font-weight: 700;
    padding: 10px;
}

.eighteen {
    font-size: 18px;
}

.ten {
    font-size: 10px;
}

p {
    margin: 15px;
}